.Investors{
	height: 100vh;
	padding-left: 5vw;
	padding-right: 5vw;
	overflow-wrap: break-word;
	display: flex;
	justify-content: space-between;
/*	column-gap: 1cm;*/
}

.right-investors{
	display: flex;
	flex-direction: column;
	row-gap: .7cm;

}
.left-investors{
	padding-top: 20vh;
/*	padding-bottom: 20vh;*/
}
.upper-right-investors{
	width: 881px;
	height: 246px;
	position: relative;
	overflow-wrap: break-word;
	
	border-radius: 10px;
	padding-top: 21vh;

}
.ill-text{
	padding-left: 10vw;
	padding-top: 20vh;
}
h2{
	margin: 0px 0px;
}
.upper-right-investors::after{
	content: '';
    position: absolute;
    width: 33px;
    height: 174px;
    background-color: blueviolet;
    top: 25vh;
    bottom: 0;
/*    left: 50%;*/
    margin-left: -480px;
/*    margin-bottom: 0px;*/
    border-radius: 7px;

}
.upper-i{
	text-align: left;
/*	padding-left: 5vw;*/
/*	padding-right: 5vw;*/
}

.lower-right-investors{
	width: 881px;
	height: 246px;
	position: relative;
	overflow-wrap: break-word;
	border:none;
	background-color: lightgray;
	
}
.lower-i{
	text-align: left;
	padding: 5vh;
}

