.Join{
	height: 100vh;
	display: flex;
	justify-content: space-between ;
	column-gap: 1cm;
}

.left-join{
	
	display: flex;
	position: relative;
	height: 613px ;
	flex-direction: column;
	row-gap: .5cm;
	margin-top: 10vh;
/*	margin-bottom: 20vh;*/
	background-color: slateblue;

}
.left-join::after{
	content: '';
    position: absolute;
    width: 33px;
    height: 511px;
    background-color: blueviolet;
    top: 50px;
    bottom: 0;
    margin-left: 1010px;
    border-radius: 7px;
}
h1{
	margin: 0px 0px;
}
.upper-left-join{
	position: relative;
	width: 1003px;
	overflow-wrap: break-word;
	padding-top: 10vh;
	padding-bottom: 5vh;

}
