@media screen and (max-width:900px){
	.right-join{
		display: none;
	}
	.left-join{
		width: 100vw;
	}
	.lower-left-join::after{
		width: 0px;
	}
	.upper-left-join::after{
		width: 0px;
	}
	.left-u-j{
		width: 100vw;
		text-align: center;
		font-size: 40px;
	}
	.left-l-text{
		width: 90vw;
		padding-left: 5vw;
		padding-right: 5vw;
		text-align: center;
	}
	
	

}
